.banner{
    
    background-size:cover;
    height: 450px;
    color: #fff;
    
}

.content{
    padding-top: 140px;
    height: 200px;
    padding-left: 20px;

}
.title{
    font-size: 2.5rem;
    font-weight: 600;
    padding-bottom: 0.3rem;
    
}
.button{
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(13, 225, 10, 0.6);
    cursor: pointer;
    margin-right: 2rem;
}
.button:hover{
    color: rgb(238, 240, 244);
    background-color: #1843ef;
}
.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}