
.row{
    margin-left: 20px;
    color : #fff;
}
.row h2{
    padding-top: 10px;
}

.posters{
    display: flex;
    
    /* flex-wrap: wrap; */
    padding:20px;
    overflow-x:scroll;
    overflow-y:hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    max-height: 200px;
    margin-right: 10px;
    margin: 15px;
    border-radius: 15px;
    cursor: pointer;
}
.smallPoster{
    max-height: 150px;
    display: flex;
    margin: 10px;
    border-radius: 10px;
    cursor: pointer;


}
.poster:hover{
    transform:scale(1.1)
}
.smallPoster:hover{
    transform:scale(1.2)
}
